$icon-button-size: ($icon-size * 2);

@include pulsate("icon-button-focus-ripple-pulsate");

.mui-icon-button {

  * { @include ease-out(); }
  position: relative;
  padding: ($icon-size / 2);

  .mui-focus-ripple {
    .mui-focus-ripple-inner {
      @include pulsate-animation("icon-button-focus-ripple-pulsate");
      background-color: rgba(0,0,0,0.1);
      box-shadow: 0px 0px 0px 1px rgba(0,0,0,0.1);
      border: solid 6px $transparent;
      background-clip: padding-box;
    }
  }

  .mui-icon-button-tooltip {  
    margin-top: ($icon-button-size + 4);
  }

  &.mui-is-disabled {
    color: lighten($body-text-color, 75%);
  }
}

.mui-dark-theme {
  .mui-touch-ripple {
    .mui-ripple-circle-inner {
      background-color: rgba(255,255,255,0.3);
    }
  }

  .mui-focus-ripple {
    .mui-focus-ripple-inner {
      background-color: rgba(255,255,255,0.3);
      box-shadow: 0px 0px 0px 1px rgba(255,255,255,0.3);
    }
  }
}
