@font-face {
  font-family: 'material-ui-icons';
  src: url($mui-icons-font-path + 'fonts/material-ui-icons.eot');
}
@font-face {
  font-family: 'material-ui-icons';
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'),
     url(data:application/font-woff;charset=utf-8;base64,) format('woff');
  font-weight: normal;
  font-style: normal;
}

[class^="mui-icon-"], [class*=" mui-icon-"] {
  font-family: 'material-ui-icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.mui-icon-sort:before {
  content: "\e600";
}
.mui-icon-github:before {
  content: "\e625";
}
.mui-icon-arrow-drop-right:before {
  content: "\e60b";
}
.mui-icon-pie:before {
  content: "\e610";
}
